import { limit } from '../../constants/const.js'
const db = wx.cloud.database()
import { transQuestion } from '../../utils/transData.js'
export default {
	data() {
		return {
			type: 'pratice',
			list: [],
			answers: [],
			corrects: 0
		};
	},
	methods: {
		radioChange(e, i) {
			let { value } = e.detail
			this.answers[i].answer = value
		},
		
		submit() {
			this.corrects = 0
			let len = this.list.length
			for (let i = 0; i < len; i++) {
				if (this.list[i].answer === this.answers[i].answer) {
					this.corrects++
				}
			}
			
			if(this.type === 'exam') {
				this.addHistory()
				this.deleteExam()
			}
			
			uni.navigateTo({
				url: `../result/result?corrects=${this.corrects}&type=${this.type}`
			})
		},
		
		async getList() {
			let r = Math.random() * 10
			r = r === 0 ? r+1 : r
			let skip = (~~r) * 10
			
			try{
				let res = await db.collection('questions').skip(skip).limit(limit).get()
				this.list = transQuestion(res.data)
				this.answers = this.list.map(item => {
					let obj = {
						...item
					}
					obj.answer = 'A'
					return obj
				})
			}catch(e){
				console.log(e)
			}
		},
		
		async addHistory() {
			let res = await db.collection('history').add({
				data: {
					account: this.$store.state.user.account,
					correct: this.corrects,
					score: this.corrects * 10,
					time: new Date()
				}
			})
		},
		
		async deleteExam() {
			console.log(this.$store.state.user.account)
			await db.collection('exam').where({
				account: this.$store.state.user.account
			}).remove()
		}
		
		
	},
	onLoad(opt) {
		this.type = opt.type
		this.getList()
	}
}
